/*colors*/
$w: #FFFFFF;
$b: #18191A;
$blu: #061A40;
$gr: #45474D;
$grl: #808285;
$grsl: #F4F7FB;
$or: #EB7C1E;
/*colors*/
/*fonts*/
$rl: 'Raleway', sans-serif;
$os: 'Open Sans', sans-serif;
@media screen and (max-width: 1560px) {
    .h-adress {
        font-size: 12px;
        padding-right: 20px;
    }
    .h-top {
        .phone-wp {
            padding-right: 30px;
        }
        .phone {
            font-size: 16px;
        }
    }
    .h-menu {
        li {
            margin-right: 4px;
            a {
                font-size: 12px;
            }
        }
    }
    .h-bot {
        .search-inp {
            width: 100%;
        }
    }
    .sidebar {
        .site-list {
            width: 100%;
            padding: 49px 15px 43px 15px;
            &:before {
                width: 100%;
            }
            .def-btn.del {
                &:before {
                    left: 15px;
                }
            }
            ul {
                li {
                    a {
                        font-size: 15px; 
                    }
                }
            }
        }
    }
    .main-form {
        .form-body {
            width: 100%;
            .top-form-wp {
                font-size: 0;
                margin: 0 -8px;
            }
            .inp-wp {
                width: 33.33%;
                margin: 0 0 32px 0;
                padding: 0 8px;
                &:last-of-type {
                    margin: 0 0 32px 0;
                }
                label {
                    left: 24px;
                }
            }
        }
    }
    .service-item {
        width: 368px;
        .descr {
            height: 394px;
        }
        .def-btn {
            font-size: 13px;
            width: 152px;
            padding: 0 5px;
            margin-right: 16px;
            margin-top: 10px;
        }
    }
    .tovar-item {
        width: 271px;
    }
    .tovar-block {
        .tovar-image {
            width: 50%;
        }
    }
    .contacts-form-map-block {
        .contacts-form-map {
            width: 50%;
        }
        .contacts-map {
            width: 50%;
        }
    }
    .left-img-content {
        padding: 0 25px 25px 0;
    }
    .right-img-content {
        padding: 0 0 25px 25px;
    }
}
@media screen and (max-width: 1200px) {
    .logo-wp {
        width: 26%;
    }
    .logo {
        span {
            font-size: 28px;
            line-height: 30px;
        }
        p {
            font-size: 12px;
            line-height: 14px;
        }
    }
    .h-contact-wp {
        width: 74%;
        .def-btn {
            font-size: 13px;
        }
    }
    .h-menu-wp {
        width: 75%;
    }
    .h-menu li a {
        font-size: 12px;
        padding: 0 4px;
    }
    .header-search-wp {
        width: 25%;
    }
    header {
        .search-inp {
            input {
                width: 85%;
            }
            button {
                width: 15%;
            }
        }
    }
    .main-form {
        .confid {
            width: 400px;
            display: block;
            margin: 0 auto 20px;
        }
        .bot-form-wp {
            display: block;
        }
        .subm-btn-wp {
            display: block;
            padding-left: 0;
            vertical-align: top;
            text-align: center;
        }
    }
    footer {
        .rights {
            width: 100%;
        }
    }
    .service-item {
        width: 302px;
        .text-item {
            font-size: 14px;
        }
        .descr {
            height: 464px;
        }
        .def-btn {
            margin: 0 auto;
            display: block;
            &:last-of-type {
                margin: 10px auto 0;
            }
        }
        .btn-wp {
            height: 90px;
        }
    }
    header {
        .search-inp {
            input {
                width: 88%;
            }
            button {
                width: 12%;
            }
        }
    }
    .banner-item {
        .descr {
            max-width: 55%;
            top: 75px;
        }
    }
    .owl-dots {
        .owl-dot {
            span {
                width: 12px;
                height: 12px;
            }
        }
    }
    .banner-item {
        .def-btn {
            width: 140px;
            height: 40px;
            line-height: 40px;
            font-size: 14px;
        }
    }
    .news-item {
        .descr {
            padding-top: 0;
        }
    }
    .tovar-item {
        width: 299px;
        &:nth-child(3n) {
            margin: 0 16px 16px 0;
        }
        &:nth-child(2n) {
            margin: 0 0 16px;
        }
    }
    .photo-item {
        width: 303px;
    }
    .more-news-list {
        .news-item-wp {
            width: 33.33%;
        }
    }
    .wrap_video {
        width: 330px;
    }
    .left-img-content, .right-img-content {
        display: block;
        padding: 0;
        float: none;
        text-align: center;
        margin: 0 0 20px;
    }
    .text-img-block {
        margin-bottom: 30px;
    }
    .text-diplom-block {
        .left-diplom {
            img {
                margin: 0;
            }
        }
        .text_diplom {
            padding-left: 30px;
        }
    }
}
@media screen and (min-width: 991px) {
    .popup-filter {
        display: block!important;
    }
}
@media screen and (max-width: 991px) {
    .h-menu-wp {
        width: 60%;
    }
    .header-search-wp {
        width: 40%;
    }
    .h-menu {
        display: none;
    }
    .h-bot {
        .nav-icon {
            display: inline-block;
            margin-top: 3px;
        }
    }
    .search-inp {
        input {
            width: 88%;
        }
        button {
            width: 12%;
        }
    }
    .h-top {
        .phone-wp {
            padding-right: 0;
        }
    }
    .h-contact-wp {
        .row-flex {
            margin-top: 0;
        }
        .btn-wp {
            display: block;
            padding-top: 10px;
        }
    }
    header.fixed {
        .h-bot {
            .h-menu-wp {
                width: 15%;
            }
            .header-search-wp {
                width: 85%;
            }
        }
        .def-btn {
            &.recall {
                line-height: 39px !important;
                font-size: 12px;
            }
        }
        .phone-wp {
            .phone {
                font-size: 12px;
                margin-right: 15px;
            }
        }
    }
    .sidebar {
        .site-list {
            display: none;
        }
    }
    .text_edit {
        .right-img {
            width: 40%;
        }
        ul {
            padding: 0 0 0 32px;
            li {
                font-size: 14px;
                line-height: 23px;
                &:before {
                    font-size: 9px;
                    top: 9px;
                }
            }
        }
        p {
            font-size: 14px;
            line-height: 22px;
        }
    }
    .f-btn-wp {
        text-align: right;
        .def-btn {
            width: 180px;
        }
        .foot-adress {
            margin: 7px 0;
        }
        .phone {
            &:first-of-type {
                margin-bottom: 10px;
            }
        }
    }
    .crumbs-list {
        .crumbs-ul {
            li {
                font-size: 11px;
                a {
                    font-size: 11px; 
                }
            }
        }
    }
    .mob-filter-btn {
        display: block;
        width: 220px;
        margin: 0 auto 30px;
    }
    .popup-filter {
        padding: 50px 20px 31px;
        width: 100%;
        height: 100%;
        height: 100%;
        background: white;
        margin: 0;
        overflow: auto;
        .def-btn.del {
            padding-left: 24px;
            width: 220px;
            text-align: center;
            &:before {
                left: 4px;
            }
        }
        .filter-category-block {
            width: 500px;
            margin: 0 auto;
        }
    }
    .banner-item {
        .slide-title, .slide-title-sub {
            font-size: 31px;
        }
        .def-btn {
            margin-top: 28px;
        }
    }
    .service-item {
        width: 351px;
    }
    .news-item {
        display: inline-block;
        width: 49%;
        vertical-align: top;
        margin-right: 1%;
        &:nth-child(2n) {
            margin-left: 1%;
            margin-right: 0;
        }
        .prev {
            display: block;
            width: 100%;
            padding-bottom: 15px;
            text-align: center;
        }
        .descr {
            display: block;
            padding: 0;
        }
        .news-title {
            height: 48px;
            overflow: hidden;
        }
        .news-txt {
            height: 68px;
            overflow: hidden;
        }
    }
    .mob-filter-btn {
        display: block;
        width: 220px;
        margin: 30px auto 15px;
    }
    .tovar-item {
        width: 351px;
    }
    .tovar-block {
        display: block;
        .tovar-image {
            display: block;
            width: auto;
            margin-bottom: 15px;
        }
        .tovar-descr {
            display: block;
            padding: 0;
        }
        .def-btn {
            width: 250px;
            margin: 0 auto;
            display: block;
        }
    }
    .wrap_video {
        margin: 20px auto;
        display: block;
        float: none;
        width: 350px;
    }
    .photo-item {
        width: 350px;
    }
    .more-news-list {
        .news-item {
            width: 100%;
        }
        .news-item-wp {
            width: 50%;
        }
    }
    .article-container {
        padding: 0;
    }
    .contacts-form-map-block {
        display: block;
        .contacts-form-map {
            display: block;
            width: 100%;
        }
        .contacts-map {
            display: block;
            height: 500px;
            width: 100%;
        }
    }
    .contacts-table {
        td {
            width: 40%;
            font-size: 14px;
            line-height: 22px;
            padding: 5px;
        }
        a {
            font-size: 14px;
            line-height: 22px;
        }
    }
    .city-tag {
        font-size: 12px;
    }
    .text-diplom-block {
        .left-diplom {
            width: 245px;
        }
    }
    .base-form.dealer-form {
        padding: 20px;
        width: 90%;
    }
}
@media screen and (max-width: 767px) {
    .h-menu-wp {
        width: 20%;
    }
    .header-search-wp {
        width: 80%;
    }
    .h-bot {
        .search-inp {
            width: 300px;
        }
    }
    .service-item {
        width: 47.3%;
    }
    .catalog-item {
        .catalog-item-title {
            height: auto;
        }
    }
    .tovar-item {
        width: 49%;
        margin: 0 1% 16px 0;
        &:nth-child(even) {
            margin: 0 0 16px 1%;
        }
        &:nth-child(odd) {
            margin: 0 1% 16px 0;
        }
    }
    .photo-item {
        width: 49%;
    }
    .text-diplom-block {
        display: block;
        margin-bottom: 48px;
        .left-diplom {
            display: block;
            width: auto;
            margin-bottom: 20px;
            text-align: center;
        }
        .text_diplom {
            display: block;
            padding-left: 0;
        }
    }
    .base-form.contacts-form-map {
        width: 100%;
        .form-body {
            .inp-wp {
                margin-bottom: 15px;
            }
        }
    }
}
@media screen and (max-width: 650px) {
    .text_edit {
        h1 {
            font-size: 28px;
            line-height: 30px;
        }
        h2 {
            font-family: $os;
            font-style: normal;
            font-weight: 500;
            font-size: 22px;
            line-height: 25px;
            margin-top: 0;
            margin-bottom: 20px;
            display: block;
            color: #2D3140; 
        }
        h3 {
            font-family: $os;
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 20px;
            margin-top: 0;
            margin-bottom: 15px;
            display: block;
            color: #2D3140; 
        }
        p {
            font-family: $rl;
            font-weight: normal;
            font-size: 14px;
            line-height: 22px;
            color: $b;
            margin-top: 0;
            margin-bottom: 14px;
            a {
                color: #7148FA;
                text-decoration: none;
                &:hover, &:focus {
                    color: $or;
                    text-decoration: none;
                }
            }
        }
        .right-img {
            float: none;
            margin: 0 auto 20px;
            width: auto;
        }
    }
    .h-top {
        padding: 35px 0 13px;
    }
    .h-bot {
        .search-inp {
            width: 220px;
            input {
                width: 85%;
            }
            button {
                width: 15%;
            }
        }
    }
    header {
        .logo-wp {
            width: 100%;
            text-align: center;
        }
        .h-contact-wp {
            width: 100%;
            position: relative;
            .def-btn {
                display: none;
            }
            .chahge-lang {
                position: absolute;
                right: -10px;
                top: -85px;
            }
        }
    }
    header.fixed {
        .phone-wp {
            display: none;
        }
    }
    .mobile-info-block {
        display: block;
    }
    .crumbs-list {
        margin-bottom: 15px;
        .crumbs-ul {
            overflow: hidden;
            li {
                font-size: 12px;
                display: inline-block;
                float: left;
                clear: both;
                margin: 0;
                &:last-child {
                    margin: 0;
                }
                a {
                    font-size: 12px;
                    padding-left: 0;
                    &:after {
                        content: '\e5db';
                        font-family: Material Icons;
                        font-size: 13px;
                        right: -8px;
                    }
                }
            }
        }
    }
    .text_edit {
        table.character {
            td {
                display: block;
                font-size: 14px;
                &:first-child {
                    display: block;
                }
            }
        }
    }
}
@media screen and (max-width: 610px) {
    .service-item {
        width: 270px;
        display: block;
        margin: 0 auto 20px;
    }
    .banner-item {
        .descr {
            max-width: 66%;
            top: 60px;
        }
    }
    .banner-item {
        .slide-title, .slide-title-sub {
            font-size: 27px;
            line-height: 30px;
        }
        .descr {
            max-width: 72%;
        }
    }
}
@media screen and (max-width: 560px) {
    .main-form {
        .form-body {
            text-align: center;
            .top-form-wp {
                margin: 0;
            }
            .inp-wp {
                width: 270px;
                margin: 0 0 32px 0;
                padding: 0;
                label {
                    left: 15px;
                }
                input:focus + label, input + label.show {
                    left: 2px;
                }
            }
            .wrap-textarea {
                width: 270px;
                margin: 0 auto;
                textarea:focus + label, textarea + label.show {
                    left: 2px;
                }
            }
        }
        .confid {
            width: 270px;
            p {
                font-size: 10px;
            }
        }
        .subm-btn-wp {
            .def-btn {
                width: 270px;
                font-size: 14px;
            }
        }
    }
    .popup-filter {
        .filter-category-block {
            width: 270px;
        }
    }
    .news-item {
        display: block;
        width: 275px;
        margin: 0 auto 16px;
        &:nth-child(2n) {
            margin: 0 auto 16px;
        }
        .news-title {
            height: auto;
        }
        .news-txt {
            height: auto;
        }
    }
    .tovar-item {
        width: 275px;
        display: block;
        margin: 0 auto 16px;
        &:nth-child(even) {
            margin: 0 auto 16px;
        }
        &:nth-child(odd) {
            margin: 0 auto 16px;
        }
    }
    .photo-item {
        width: 100%;
        text-align: center;
    }
    .more-news-list {
        .news-item-wp {
            width: 100%;
        }
        .news-item {
            width: 270px;
            margin: 0 auto 16px;
        }
    }
    .wrap_video {
        width: 95%;
    }
}
@media screen and (max-width: 480px) {
    .logo-foot-wp {
        text-align: center;
    }
    footer {
        .logo {
            display: block;
            margin: 0 0 15px;
        }
    }
    .col-xxs {
        width: 100%;
    }
    .hidden-xxs {
        display: none;
    }
    .popup {
        width: 95%;
        padding: 15px;
    }
    .base-form.popup {
        .title-base-big {
            font-size: 28px;
            line-height: 30px;
        }
        form {
            p {
                font-size: 14px;
                line-height: 20px;
                margin: 0 0 15px;
            }
        }
        .form-body {
            .inp-wp {
                margin-bottom: 15px;
            }
        }
        .wrap-textarea {
            label {
                top: 17%;
            }
        }
        .textarea-default {
            height: 90px;
        }
        .confid {
            p {
                font-size: 10px;
                line-height: 15px;
                margin: 0;
            }
        }
    }
    .fancybox-close-small {
        top: 8px;
        right: 8px;
    }
    .pagin-list {
        .pagin-ul {
            li {
                a {
                    width: 40px;
                    height: 32px;
                    line-height: 34px;
                    font-size: 14px;
                }
            }
        }
    }
    .banner-item {
        .slide-title, .slide-title-sub {
            font-size: 25px;
            line-height: 28px;
        }
        .slide-descr {
            font-size: 12px;
            line-height: 14px;
        }
    }
    header {
        .h-contact-wp {
            .chahge-lang {
                right: -35px;
                top: -97px;
            }
        }
    }
    .catalog-item {
        .catalog-item-title {
            height: 40px;
        }
    }
    .tovar-slide-item {
        .trigger-title {
            font-size: 15px;
            padding: 0 16px 0 46px;
            &:before {
                font-size: 24px;
                line-height: 24px;
                left: 15px;
            }
        }
        .tovar-slide-text {
            &.open-slide-text {
                padding: 15px;
            }
        }
    }
    .download-btn {
        font-size: 16px;
        line-height: 22px;
        margin-bottom: 10px;
        &:before {
            top: 0;
        }
    }
    .bottle-item {
        margin-right: 15px;
    }
    .contacts-form-map-block {
        .contacts-form-map {
            padding: 15px;
        }
    }
    .contacts-table {
        td {
            display: block;
            width: 100%;
        }
    }
    .contacts-form-map-block {
        .contacts-map {
            height: 350px;
        }
    }
    .sertificate-item {
        width: 270px;
        margin: 0 auto 24px;
    }
    .text-img-block {
        padding: 0;
    }
}
@media screen and (max-width: 420px)  {
    .mobile-info-block {
        top: -200px;
    }
}